<template>
  <div class="administrator-home-page-container">
    <ul class="list-style flex flex-wrap">
      <li
        class="list-item"
        v-for="(item, index) in gridList"
        :key="index"
        @click="handleGoToNavItem(item.route)"
      >
        <NavCard
          :title="item.title"
          :color="item.color"
          :iconUrl="item.iconUrl"
        />
      </li>
    </ul>

    <DataBoardCard />

    <!-- 宣讲区域覆盖人数排名 -->
    <div class="wrap">
      <TopList
        title="宣讲区域覆盖人数排名"
        :list="topListCoverCmp"
        @click="goToPreachRegionalCoverageNumberRank"
      />
    </div>

    <!-- 新增宣讲覆盖人数 -->
    <div class="wrap">
      <ColumnChart
        class="card-row-style"
        title="新增宣讲覆盖人数"
        :list="dateCoverListCmp"
        @click="goToAddPreachCovers"
      />
    </div>

    <!-- 授课老师宣讲数据排名 -->
    <div class="wrap">
      <TableList
        class="card-row-style"
        title="授课老师宣讲数据排名"
        :columns="cloumnListPreach"
        :list="instructorListCmp"
        @click="goToTeacherSpeakingRank"
      />
    </div>

    <!-- 近期承办单位列表 -->
    <div class="wrap">
      <TopList
        class="card-row-style"
        title="近期承办单位列表"
        :list="topListUndertakeCmp"
        :showSort="false"
        @click="goToRecentUndertakingUnit"
      />
    </div>

    <!-- 近期宣讲详情 -->
    <div class="wrap">
      <TableList
        class="card-row-style"
        title="近期宣讲详情"
        :columns="cloumnListRecentAboutDetails"
        :list="preachListRecentAboutDetailsCmp"
        :showSort="false"
        @click="goToRecentAboutDetails"
      />
    </div>

    <!-- 近期宣讲区域列表 -->
    <div class="wrap">
      <TopList
        class="card-row-style"
        title="近期宣讲区域列表"
        :list="recentPreachAreaListCmp"
        :showSort="false"
        @click="goToPreachingArea"
      />
    </div>
  </div>
</template>

<script>
export default {
  components: {
    NavCard: () => import("./components/NavCard"),
    Card: () => import("@/components/Card"),
    DataBoardCard: () => import("@/components/DataBoardCard"),
    TableList: () => import("@/components/TableList"),
    TopList: () => import("@/components/TopList"),
    ColumnChart: () => import("@/components/ColumnChart"),
  },
  data() {
    return {
      gridList: [
        {
          title: "新开活动",
          color: "#FFA561",
          iconUrl: require("@/assets/icon/feedback_icon.png"),
          route: "/adminNewActivity",
        },
        // TODO: 下个版本迭代, 目前不开放
        // {
        //   title: "活动补录",
        //   color: "#FF5C64",
        //   iconUrl: require("@/assets/icon/order_icon.png"),
        // },
        {
          title: "成员管理",
          color: "#5DDAC4",
          iconUrl: require("@/assets/icon/teachers_icon.png"),
          route: `/lecturerManagement`,
        },
        {
          title: "成员审核",
          color: "#8486F8",
          iconUrl: require("@/assets/icon/build_team_icon.png"),
          route: "/membersAudit",
        },
        {
          title: "活动列表",
          color: "#0077FF",
          iconUrl: require("@/assets/icon/symposium_icon.png"),
          route: "/activeDynamic",
        },
      ],
      topList: [
        {
          name: "福州市台江区上海街道",
          num: 40000,
        },
        {
          name: "福州市台江区上海街道",
          num: 40000,
        },
        {
          name: "福州市台江区上海街道",
          num: 40000,
        },
        {
          name: "福州市台江区上海街道",
          num: 40000,
        },
      ],
      // 宣讲数据排名 column
      cloumnListPreach: [
        {
          label: "姓名",
          keyName: "name",
        },
        {
          label: "听课人数",
          keyName: "visitorStr",
        },
        {
          label: "场次",
          keyName: "roundStr",
        },
      ],
      // 近期承办单位列表
      topListUndertake: [
        {
          label: "福州市台江区上海街道社区",
          value: "2022年2月25日",
        },
        {
          label: "福州市台江区上海街道社区",
          value: "2022年2月25日",
        },
        {
          label: "福州市台江区上海街道社区",
          value: "2022年2月25日",
        },
        {
          label: "福州市台江区上海街道社区",
          value: "2022年2月25日",
        },
      ],
      // 近期宣讲详情 column
      cloumnListRecentAboutDetails: [
        {
          label: "宣讲主题",
          keyName: "title",
        },
        {
          label: "主讲人",
          keyName: "name",
        },
        {
          label: "听课人数",
          keyName: "visitor",
        },
      ],
      // 近期列表
      recentList: [],
      // 覆盖列表
      coverList: [],
      // 授课老师排名列表
      instructorList: [],
      // 日期覆盖列表
      dateCoverList: [],
    };
  },

  computed: {
    // 近期宣讲区域列表
    recentPreachAreaListCmp() {
      const arr = [];
      this.recentList.forEach((item) => {
        arr.push({
          label: this.handleAreaText(item),
          value: this.handleDateFormat(item.date),
        });
      });
      return arr;
    },
    // 近期承办单位列表
    topListUndertakeCmp() {
      const arr = [];
      this.recentList.forEach((item) => {
        arr.push({
          label: item.host_info.name,
          value: this.handleDateFormat(item.date),
        });
      });
      console.log("近期承办单位列表arr :>> ", arr);
      return arr;
    },
    // 近期宣讲详情 listData
    preachListRecentAboutDetailsCmp() {
      const arr = [];
      this.recentList.forEach((item) => {
        arr.push({
          ...item.instructor_info,
          title: item.title,
          visitor: `${item.visitor}人`,
        });
      });
      console.log("近期宣讲详情arr :>> ", arr);
      return arr;
    },
    // 排行榜:覆盖
    topListCoverCmp() {
      const arr = [];
      this.coverList.forEach((item) => {
        console.log("item :>> ", item);
        for (const arrItem of arr) {
          if (arrItem.name === item.city) {
            arrItem.value += item.visitor;
            return;
          }
        }
        arr.push({
          label: this.handleAreaText(item),
          value: `${item.visitor}人`,
        });
      });

      console.log("排行榜:覆盖arr :>> ", arr);
      return arr;
    },
    // 授课老师排名列表
    instructorListCmp() {
      const arr = [];
      for (const key in this.instructorList) {
        arr.push({
          rankingName: `第${parseInt(key) + 1}名`,
          name: this.instructorList[key].instructor_info.name,
          visitorStr: `${this.instructorList[key].visitor}人`,
          roundStr: `${this.instructorList[key].host_time}场`,
        });
      }
      return arr;
    },
    // 排行榜:新增宣讲覆盖
    dateCoverListCmp() {
      const arr = [];

      this.dateCoverList.forEach((item) => {
        arr.push({
          label: this.$dateFormat(item.date, "m/dd"),
          num: item.visitor,
        });
        console.log("arr :>> ", arr);
      });

      return arr;
    },
  },

  created() {
    this.recentListAPI();
    this.coverListAPI();
    this.instructorListAPI();
    this.dateCoverListAPI();
  },

  methods: {
    /**
     * tabsNum 选中项发生改变
     */
    tabsNumChange(val) {
      console.log("val :>> ", val);
    },
    /**
     * 每一页轮播结束后触发
     */
    changeSwipe(val) {
      this.$emit("change", val);
    },
    /**
     * 切换到指定位置
     */
    activeSwipe(active) {
      this.$refs.swipe.swipeTo(active);
    },
    /**
     * 近期列表API
     */
    async recentListAPI() {
      const res = await this.$request("lecture.recentList");
      console.log("近期列表API_res :>> ", res);
      this.recentList = res;
    },
    /**
     * 覆盖列表API
     */
    async coverListAPI() {
      const res = await this.$request("lecture.coverList");
      console.log("覆盖列表API_res :>> ", res);
      this.coverList = res;
    },
    /**
     * 授课老师排名列表API
     */
    async instructorListAPI() {
      const res = await this.$request("lecture.instructorList");
      console.log("授课老师排名列表API_res :>> ", res);
      this.instructorList = res;
    },
    /**
     * 日期覆盖列表API
     */
    async dateCoverListAPI() {
      const data = {
        type: "month",
      };
      const res = await this.$request("lecture.dateCoverList", data);
      console.log("日期覆盖列表API_res :>> ", res);
      this.dateCoverList = res;
    },
    /**
     * 格式化日期
     */
    handleDateFormat(date) {
      return this.$dateFormat(date, "yyyy年mm月dd日");
    },
    /**
     * 处理区域数据
     */
    handleAreaText(item) {
      return `${item.province}${item.city}${item.district}${item.street}`;
    },

    /**
     * 跳转:近期宣讲区域列表
     */
    goToPreachingArea() {
      this.$router.push({
        path: `/preachingArea`,
      });
    },
    /**
     * 跳转:近期承办单位列表
     */
    goToRecentUndertakingUnit() {
      this.$router.push({
        path: `/recentUndertakingUnit`,
      });
    },
    /**
     * 跳转:近期宣讲详情
     */
    goToRecentAboutDetails() {
      this.$router.push({
        path: `/recentAboutDetails`,
      });
    },
    /**
     * 跳转:宣讲区域覆盖人数排名
     */
    goToPreachRegionalCoverageNumberRank() {
      this.$router.push({
        path: `/preachRegionalCoverageNumberRank`,
      });
    },
    /**
     * 跳转:授课老师宣讲数据排名
     */
    goToTeacherSpeakingRank() {
      this.$router.push({
        path: `/teacherSpeakingRank`,
      });
    },
    /**
     * 跳转:新增宣讲覆盖人数
     */
    goToAddPreachCovers() {
      this.$router.push({
        path: `/addPreachCovers`,
      });
    },

    handleGoToNavItem(route) {
      this.$router.push({
        path: route,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.administrator-home-page-container {
  .list-style {
    padding: px(24) px(24) px(0);
    background: #fff;
    .list-item {
      margin: 0 0 px(24) 0;
      width: px(339);
    }
  }
}
.wrap {
  margin-top: px(24);
}
</style>
